$(document).ready(function () {
    // Swiper: Slider
    new Swiper('.js-careers-gallery', {
        loop: true,
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev',
        slidesPerView: 1,
        paginationClickable: true,
        spaceBetween: 20,
        speed: 1000,
        autoplay: {
            delay: 1500,
        },
        breakpoints: {
            1920: {
                slidesPerView: 3,
                spaceBetween: 24
            },
            1028: {
                slidesPerView: 4.3,
                spaceBetween: 24
            },
            480: {
                slidesPerView: 1,
                spaceBetween: 10
            }
        }
    });

    // reverse slider

    new Swiper('.js-careers-gallery-reverse', {
        loop: true,
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev',
        slidesPerView: 1,
        paginationClickable: true,
        spaceBetween: 20,
        speed: 1000,
        autoplay: {
            delay: 1500,
        },
        breakpoints: {
            1920: {
                slidesPerView: 3,
                spaceBetween: 24
            },
            1028: {
                slidesPerView: 4.3,
                spaceBetween: 24
            },
            480: {
                slidesPerView: 1,
                spaceBetween: 10
            }
        }
    });

    // campaign slider
    const campaignSlider = new Swiper('.js-campaign', {
        loop: false,
        speed: 1000,
        navigation: {
            nextEl: ".js-arrow-left",
            prevEl: ".js-arrow-right",
        },
        slidesPerView: 1,
        paginationClickable: true,
        spaceBetween: 20,
        // autoplay: {
        //     delay: 1500,
        // },
        breakpoints: {
            1920: {
                slidesPerView: 3,
                spaceBetween: 24
            },
            1028: {
                slidesPerView: 3,
                spaceBetween: 24
            },
            768: {
                slidesPerView: 1.7,
                spaceBetween: 10
            },
            480: {
                slidesPerView: 1,
                spaceBetween: 10
            }
        }
    });

    $(".mr-careers-campaigns .js-cuurent-slide").text(campaignSlider.realIndex + 1);

    $(".mr-careers-campaigns .js-total-slides").text(Math.floor((($(".js-campaign .swiper-slide").length))))
    campaignSlider.on('slideChange', function () {
        $(".mr-careers-campaigns .js-cuurent-slide").text(campaignSlider.realIndex + 1);
    });
});
